<template>
  <div class="home">
    <Sidebar></Sidebar>

    <section class="main_section">
      <div class="main_container">

        <div class="section-2">
          <div class="d-flex justify-content-between">
            <div class="dash_card_title">
              insight create
            </div>
          </div>

          <form @submit.prevent="create()">
            <div class="row">

              <div class="col-xl-9">

                <div class="main_cards">
                  <div>
                    <b-tabs content-class="mt-3">
                      <b-tab title="Eng" active>

                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                title
                              </label>
                              <div class="login_input">
                                <input v-model="title" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                description
                              </label>
                              <div class="login_input">
                                <input v-model="description" type="text" placeholder="">
                              </div>
                            </div>
                            <div class="col-xl-6">
                              <div class="login_text_input">
                                <label>
                                  source
                                </label>
                                <div class="login_input">
                                  <input v-model="source" type="text" placeholder="">
                                </div>
                              </div>
                              </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                coin
                              </label>
                              <select name="cars" id="cars" class="form-control" v-model="coin">
                                <option v-for="item in data" :value="item.id" :key="item">{{item.name}}</option>
                              </select>
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                type
                              </label>
                              <select name="cars" id="cars" class="form-control" v-model="type">
                                <option value="news" >News</option>
                                <option value="articles" >Articles</option>
                                <option value="trending" >Trending</option>
                              </select>
                            </div>
                          </div>
                        </div>
                      </b-tab>
                      <b-tab title="Rus">
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                title
                              </label>
                              <div class="login_input">
                                <input v-model="titleru" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                        </div>
                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                description
                              </label>
                              <div class="login_input">
                                <input v-model="descriptionru" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                        </div>
                      </b-tab>
                    </b-tabs>
                  </div>
                </div>
              </div>
              <div class="col-xl-3">

                <div class="main_cards">
                  <div>
                    <label for="#">Image</label>
                    <div class="create_input">

                      <input type="file" class="form-control"
                             multiple
                             @change="trySubmitFile"
                             accept="image/*"
                             name="datafiles"
                             ref="datafiles"
                      >
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-xl-12">
              <button :disabled="isCheck" class="btn btn-primary w-100"  >
                <span v-if="!isCheck">Create </span>
                <b-spinner v-if="isCheck"></b-spinner>
              </button>
            </div>
          </form>
        </div>
      </div>

    </section>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
  components:{
    Sidebar,
  },
  data(){
    return{
      name:'',
      source:'',
      type:'',
      coin:'',
      description:'',
      title:'',


      nameru:'',
      sourceru:'',
      typeru:'',
      coinru:'',
      descriptionru:'',
      titleru:'',

      isCheck:false,

      myImages:[],
      imageUrl:[],
      data:[],
      files_name:[],
    }
  },
  mounted() {
    this.$http.get('admin/coins/',{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`
      }
    })
        .then(res=>{
          this.data=res.data
        })
  },
  methods:{
    trySubmitFile(e) {
      this.myImages=[]
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    create(){
      this.isCheck=true
      const form = new FormData();
      form.append("image", this.myImages[0]);
      form.append("title", this.title);
      form.append("description", this.description);
      form.append("coin", this.coin);
      form.append("type", this.type);
      form.append("source", this.source);
      this.$http.post('admin/insights/',form,{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'en'

        }
      })
          .then(res=>{
            this.isCheck=true
            const form = new FormData();
            form.append("title", this.titleru);
            form.append("description", this.descriptionru);
            this.$http.patch(`admin/insights/${res.data.id}/`,form,{
              headers:{
                Authorization:`Bearer ${localStorage.getItem('token')}`,
                'Accept-Language': 'ru'

              }
            })
                .then(()=>{
                  this.isCheck=false
                  this.$toast(`Success ru`, {
                    timeout: 2000,
                    type:'success'
                  });
                })
                .catch(error=>{
                  console.log(error.response.data)
                  this.isCheck=false
                  this.$toast(`${error.response.data.website}`, {
                    timeout: 2000,
                    type:'error'
                  });
                })
            this.isCheck=false
            this.$toast(`Success eng`, {
              timeout: 2000,
              type:'success'
            });
          })
          .catch(error=>{
            console.log(error.response.data)
            this.isCheck=false
            this.$toast(`${error.response.data.website}`, {
              timeout: 2000,
              type:'error'
            });
          })
    },

  }
}
</script>
