<template>
  <div class="home">
    <Sidebar></Sidebar>

    <section class="main_section">
      <div class="main_container">

        <div class="section-2">
          <div class="d-flex justify-content-between">
            <div class="dash_card_title">
              airdrop_info create
            </div>
          </div>

          <form @submit.prevent="create()">
            <div class="row">

              <div class="col-xl-9">

                <div class="main_cards">
                  <div>
                    <b-tabs content-class="mt-3">
                      <b-tab title="Eng" active>

                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                shortInfo
                              </label>
                              <div class="login_input">
                                <input v-model="name" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                about
                              </label>
                              <div class="login_input">
                                <input v-model="symbol" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                        </div>
                      </b-tab>
                      <b-tab title="Rus">

                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                shortInfo
                              </label>
                              <div class="login_input">
                                <input v-model="nameru" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                about
                              </label>
                              <div class="login_input">
                                <input v-model="symbolru" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                        </div>
                      </b-tab>
                    </b-tabs>
                  </div>
                </div>
              </div>
<!--              <div class="col-xl-3">-->

<!--                <div class="main_cards">-->
<!--                  <div>-->
<!--                    <label for="#">Icon</label>-->
<!--                    <div class="create_input">-->

<!--                      <input type="file" class="form-control"-->
<!--                             multiple-->
<!--                             @change="trySubmitFile"-->
<!--                             accept="image/*"-->
<!--                             name="datafiles"-->
<!--                             ref="datafiles"-->
<!--                      >-->
<!--                    </div>-->
<!--                  </div>-->
<!--                </div>-->
<!--              </div>-->
            </div>

            <div class="col-xl-12">
              <button :disabled="isCheck" class="btn btn-primary w-100"  >
                <span v-if="!isCheck">Create </span>
                <b-spinner v-if="isCheck"></b-spinner>
              </button>
            </div>
          </form>
        </div>
      </div>

    </section>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
  components:{
    Sidebar,
  },
  data(){
    return{
      name:'',
      symbol:'',
      about:'',
      website:'',
      whitepaper:'',
      nameru:'',
      symbolru:'',
      aboutru:'',
      websiteru:'',
      whitepaperru:'',
      nameuz:'',
      symboluz:'',
      aboutuz:'',
      websiteuz:'',
      whitepaperuz:'',
      isCheck:false,

      myImages:[],
      imageUrl:[],
      files_name:[],
    }
  },
  methods:{
    trySubmitFile(e) {
      this.myImages=[]
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    create(){
      this.isCheck=true
      const form = new FormData();

      form.append("shortInfo", this.name);
      form.append("about", this.symbol);
      this.$http.post('/admin/airdrop-info/',form,{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'en'

        }
      })
          .then(res=>{
            this.isCheck=true
            const form = new FormData();
            form.append("shortInfo", this.nameru);
            form.append("about", this.symbolru);
            this.$http.patch(`/admin/airdrop-info/${res.data.id}/`,form,{
              headers:{
                Authorization:`Bearer ${localStorage.getItem('token')}`,
                'Accept-Language': 'ru'

              }
            })
                .then(()=>{
                  this.isCheck=false
                  this.$toast(`Success ru`, {
                    timeout: 2000,
                    type:'success'
                  });
                })
                .catch(error=>{
                  console.log(error.response.data)
                  this.isCheck=false
                  this.$toast(`${error.response.data.website}`, {
                    timeout: 2000,
                    type:'error'
                  });
                })
            this.isCheck=false
            this.$toast(`Success eng`, {
              timeout: 2000,
              type:'success'
            });
          })
          .catch(error=>{
            console.log(error.response.data)
            this.isCheck=false
            this.$toast(`${error.response.data.website}`, {
              timeout: 2000,
              type:'error'
            });
          })
    },

  }
}
</script>
