<template>
  <div class="home">
    <Sidebar></Sidebar>

    <section class="main_section">
      <div class="main_container">

        <div class="section-2">
          <div class="d-flex justify-content-between">
            <div class="dash_card_title">
              Coin edit
            </div>
          </div>

          <form @submit.prevent="create()">
            <div class="row">

              <div class="col-xl-9">

                <div class="main_cards">
                  <div>
                    <b-tabs content-class="mt-3">
                      <b-tab title="Eng" active>

                        <div class="row">
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                name
                              </label>
                              <div class="login_input">
                                <input v-model="name" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                          <div class="col-xl-6">
                            <div class="login_text_input">
                              <label>
                                symbol
                              </label>
                              <div class="login_input">
                                <input v-model="symbol" type="text" placeholder="">
                              </div>
                            </div>
                          </div>
                        </div>
                      </b-tab>
                    </b-tabs>
                  </div>
                </div>
              </div>
              <div class="col-xl-3">

                <div class="main_cards">
                  
                  <img :src="imageUrl.icon" width="100%" height="300px" class="object-fit-contain" alt="">
                  <img :src="imageUrl" v-if="imageUrl.length>0" alt="" width="100%" height="300px" class="object-fit-contain">
                  <label for="icons" class="btn btn-primary w-100">Icon</label>
                  <div>
                    <div class="create_input">

                      <input type="file"  class="d-none form-control"
                             id="icons"
                             multiple
                             @change="trySubmitFile"
                             accept="image/*"
                             name="datafiles"
                             ref="datafiles"
                      >
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-xl-12">
              <button :disabled="isCheck" class="btn btn-primary w-100"  >
                <span v-if="!isCheck">Edit </span>
                <b-spinner v-if="isCheck"></b-spinner>
              </button>
            </div>
          </form>
        </div>
      </div>

    </section>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import {data} from "browserslist";
export default {
  computed: {
    data() {
      return data
    }
  },
  components:{
    Sidebar,
  },
  data(){
    return{
      name:'',
      symbol:'',
      about:'',
      website:'',
      whitepaper:'',
      nameru:'',
      symbolru:'',
      aboutru:'',
      websiteru:'',
      whitepaperru:'',
      nameuz:'',
      symboluz:'',
      aboutuz:'',
      websiteuz:'',
      whitepaperuz:'',
      isCheck:false,

      myImages:[],
      imageUrl:[],
      files_name:[],
    }
  },
  mounted() {
    this.$http.get(`admin/coins/${this.$route.params.id}/`,{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`,
        'Accept-Language': 'en'
      }
    })
        .then(res=>{
          this.name=res.data.name
          this.symbol=res.data.symbol
          this.imageUrl=res.data
        })
    this.$http.get(`admin/coins/${this.$route.params.id}/`,{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`,
        'Accept-Language': 'ru'
      }
    })
        .then(res=>{
          this.nameru=res.data.name
          this.symbolru=res.data.symbol
        })
  },
  methods:{
    trySubmitFile(e) {
      this.myImages=[]
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    create(){
      this.isCheck=true
      const form = new FormData();
      if (this.myImages.length==0){
        form.append("name", this.name);
        form.append("symbol", this.symbol);

      }
      else {
        form.append("name", this.name);
        form.append("symbol", this.symbol);
        form.append("icon", this.myImages[0]);

      }
      this.$http.patch(`admin/coins/${this.$route.params.id}/`,form,{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'en'

        }
      })
          .then(()=>{
            this.isCheck=true
            const form = new FormData();
            form.append("name", this.name);
            form.append("symbol", this.symbol);
            this.isCheck=false
            this.$toast(`Success eng`, {
              timeout: 2000,
              type:'success'
            });
          })
          .catch(error=>{
            console.log(error.response.data)
            this.isCheck=false
            this.$toast(`${error.response.data.website}`, {
              timeout: 2000,
              type:'error'
            });
          })
    },

  }
}
</script>
